import React from 'react'
import '../App.css'
export default function Card(props) {
    return (
        <div className="box">
          <span>
            Name: {props.name } 
          </span>
          <span>
            | Deparpment: {props.dept } 
          </span>
          <span>
             | Rating: {props.rating}
          </span>
        </div>
    )
}
