
import './App.css';
import {useState} from 'react';
import Card from './Components/Card';
let com2=[];

function App() {
  const [name1, setname1] = useState("");
  const [dept1, setdept1] = useState("");
  const [rat, setrat] = useState("");
  const [com1, setcom1] = useState([]);
  const [flag, setflag] = useState(1);
  function updateForm(e){
    console.log("object");
    e.preventDefault();
 
  let com3={name:name1,dept:dept1,rating:rat};
  com2.push(com3);
  setcom1(com3);
  setdept1("");
  setname1("");
  setrat("");
  console.log(com1);
  setflag(0);
  }
  function changeName(e){
    setname1(e.target.value);
  }
  function changeDept(e){
    setdept1(e.target.value);
  }
  function changeRat(e){
      setrat(e.target.value);
  }
  function goBack(){
    setflag(1);
  }
  return (
    <div className="App">
      <h1>EMPLOYEE FEEDBACK FORM</h1>
      {flag?
      <form action="/">
        <label >Name :</label>
        <input type="text" onChange={changeName} id="fname" name="fname" value={name1} /><br />
        <label >Department :</label>
        <input type="text" onChange={changeDept} id="lname" name="lname" value={dept1} /><br/>
        <label >Rating :</label>
        <input type="number" onChange={changeRat} id="lname" name="lname" value={rat} /><br /><br />
        <input className="btn" onClick={updateForm} type="submit" value="Submit" />
      </form>:
      <><div className="formContain">
  
        
        {com2.map((v,index)=><Card key={index} name={v.name} dept={v.dept} rating={v.rating}/>)}
        
      {/* <Card name="ii" dept="iii" rating={9}/> */}
       
        </div>
        <input className="btn" onClick={goBack} type="submit" value="Go Back" />
      </>}
    </div>
  );
}

export default App;
